<template>
  <div id="staffInform">
    <el-row class="search-content">
      <el-col span="24" class="stu-choice">
        <el-input class="stu-box" style="margin-left: 230px" placeholder="请输入学生姓名" v-model="nameVal" clearable></el-input>
        <el-select class="stu-box" style= "overflow: hidden; " v-model="grades.label"  placeholder="年级">
          <el-option align="center" style="font-size: 20px;" v-for="item in grades" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-select class="stu-box" style= "overflow: hidden; " v-model="classes.label"  placeholder="班级">
          <el-option align="center" style="font-size: 20px;" v-for="item in classes" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-button type="danger" class="search-button">{{searchWord[1]}}</el-button>
      </el-col>
    </el-row>

    <el-row class="stu-inform">
      <el-col>
        <el-table :data="stuInformDis" class="stu-inform-content" row-style="height:70px" >
          <el-table-column prop="num" :label="informList[0]" >
          </el-table-column>
          <el-table-column prop="name" :label="informList[1]">
          </el-table-column>
          <el-table-column prop="class" :label="informList[2]" width="170" >
          </el-table-column>
          <el-table-column prop="contact" :label="informList[3]" width="210">
          </el-table-column>
          <el-table-column prop="sex" :label="informList[4]">
          </el-table-column>
          <el-table-column prop="age" :label="informList[5]" >
          </el-table-column>
          <el-table-column prop="date" :label="informList[6]" width="150">
            <el-button  type="text" style="font-size: 18px;">查看详细</el-button>
          </el-table-column>
        </el-table>
        <el-pagination  @current-change="tableCurrentChange" style="margin-left: 500px;margin-top: 10px" page-size="3"  layout="prev,pager,next,jumper" :total="stuInform.length"  >
        </el-pagination>
      </el-col>
    </el-row>
  </div>

</template>

<script type="text/ecmascript-6">
  export default {
    name: "staffInformPage",
    data()
    {
      return {
        searchWord:['招生人员：','搜索','重置'],
        grades:[
          {value:'1',label:'1年级'},
          {value:'2',label:'2年级'},
          {value:'3',label:'3年级'},
          {value:'4',label:'4年级'},
          {value:'5',label:'5年级'},
          {value:'6',label:'6年级'},
        ],
        classes:[
          {value:'1',label:'A班'},
          {value:'2',label:'B班'},
          {value:'3',label:'C班'},
          {value:'4',label:'D班'},
          {value:'5',label:'E班'},
          {value:'6',label:'F班'},
        ],

        nameVal:[''],
        informList:['姓名','性别','所在年级和班级','学号','年龄','入学日期','操作'],
        stuInform:[
          {num:'1',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6'},
          {num:'2',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6'},
          {num:'3',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6'},
          {num:'4',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6'},
          {num:'3',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6'},
          // {num:'4',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'}
        ],
        stuInformDis:[
          {num:'1',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'},
          {num:'2',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'},
          {num:'3',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'}]
      }
    },
    methods: {
      tableCurrentChange(val){
        this.$set(this.stuInformDis,0,this.stuInform[val*3-3]);
        this.$set(this.stuInformDis,1,this.stuInform[val*3-2]);
        this.$set(this.stuInformDis,2,this.stuInform[val*3-1]);
      }
    }

  }
</script>

<style scoped>
  #staffInform{


    height: 100%;

    background-color: #ff6a00;
  }
  *{
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;

    font-size: 25px;
    overflow: hidden;
  }
  li{
    list-style-type: none;
  }
  a{
    text-decoration: none;
  }

  .el-row {
    margin-bottom: 20px;
  }
  .el-col {
    border-radius: 4px;
  }


  .search-content {
    border-radius: 4px;
    height: 220px;
    background-color: white;
    border-bottom: 1px solid #d3d3d3;
  }

  .stu-choice{
    margin-left: 20px;

    height: 220px;

  }

  .stu-box
  {
    margin-top: 80px;
    margin-left: 20px;
    width: 200px;

    font-size: 20px;
  }

  .search-button{
    position: relative;
    top: -10px;
    left: 20px;

    height: 50px;
    width: 100px;

  }

  .stu-inform{
    height: 362px;

    background-color: white;

    border-top: 1px solid #d3d3d3;
    border-radius: 4px;
  }
  .stu-inform-content{

    margin-left: 20px;
    width: 97%;

    font-size: 20px;
    line-height: 50px;
    color: gray;
  }

  .el-select >>> .el-input__inner{
    font-size: 20px;
  }


</style>



