import Router from 'vue-router'
import Vue from 'vue'
import recruitPage from "../components/recruitPage";
import ElementUI from 'element-ui'
import locale from "element-ui/lib/locale/lang/zh-CN";
import 'element-ui/lib/theme-chalk/index.css'
import financePage from "../components/financePage";
import recruitInformPage from "../components/recruitInformPage";
import recruitIndexPage from "../components/recruitIndexPage";
import financeInformPage from "../components/financeInformPage";
import staffInformPage from "../components/staffInformPage";
import stuInformPage from "../components/stuInformPage";
import checkPendPage from "../components/checkPendPage";
import unCheckPage from "../components/unCheckPage";

Vue.use(ElementUI, { locale })
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'recruitPage',
      component: recruitPage,
      children: [
        {path: '/', component: recruitIndexPage},
        {path: 'financePage',component: financePage},
        {path: 'recruitInformPage', component: recruitInformPage},
        {path: 'financeInformPage', component: financeInformPage},
        {path: 'staffInformPage',component:staffInformPage},
        {path: 'stuInformPage',component:stuInformPage},
        {path: 'checkPendPage',component:checkPendPage},
        {path: 'unCheckPage',component:unCheckPage}
      ]
    }
    // {
    //   path: '/financePage',
    //   name: 'financePage',
    //   component:financePage
    // },
    // {
    //   path:'/recruitPage/recruitInformPage',
    //   name:'recruitInformPage',
    //   component:recruitInformPage
    // }
  ]
})



