<template>
  <div id="staffInform">
    <el-row class="search-content">
      <el-col span="24" class="staff-choice">
        <el-input class="staff-box" style="margin-left: 300px" placeholder="请输入职工姓名" v-model="nameVal" clearable></el-input>
        <el-select class="staff-box" style= "overflow: hidden; " v-model="departments.label"  placeholder="请选择部门">
          <el-option align="center" style="font-size: 20px;" v-for="item in departments" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-button type="danger" class="search-button">{{searchWord[1]}}</el-button>
      </el-col>
    </el-row>

    <el-row class="staff-inform">
      <el-col>
        <el-table :data="stuInformDis" class="staff-inform-content" row-style="height:70px" >
          <el-table-column prop="num" :label="informList[0]" >
          </el-table-column>
          <el-table-column prop="name" :label="informList[1]">
          </el-table-column>
          <el-table-column prop="depart" :label="informList[2]" width="170" >
          </el-table-column>
          <el-table-column prop="contact" :label="informList[3]" width="210">
          </el-table-column>
          <el-table-column prop="sex" :label="informList[4]">
          </el-table-column>
          <el-table-column prop="age" :label="informList[5]" >
          </el-table-column>
          <el-table-column prop="salary" :label="informList[6]" width="150">
          </el-table-column>
          <el-table-column prop="dateIn" :label="informList[7]">
          </el-table-column>
        </el-table>
        <el-pagination  @current-change="tableCurrentChange" style="margin-left: 500px;margin-top: 10px" page-size="3"  layout="prev,pager,next,jumper" :total="stuInform.length"  >
        </el-pagination>
      </el-col>
    </el-row>
  </div>

</template>

<script type="text/ecmascript-6">
  export default {
    name: "staffInformPage",
    data()
    {
      return {
        searchWord:['招生人员：','搜索','重置'],
        departments:[
          {value:'1',label:'教务'},
          {value:'2',label:'学工'},
        ],
        nameVal:[''],
        informList:['工号','姓名','部门','联系方式','性别','年龄','工资','入职日期'],
        stuInform:[
          {num:'110',name:'Tony',depart:'教务',contact:'14627189301',sex:'男',age:'26',salary:'12000',dateIn:'1998-2-11'},
          {num:'210',name:'Tony',depart:'教务',contact:'14627189301',sex:'男',age:'26',salary:'12000',dateIn:'1998-2-11'},
          {num:'310',name:'Tony',depart:'教务',contact:'14627189301',sex:'男',age:'26',salary:'12000',dateIn:'1998-2-11'},
          {num:'410',name:'Tony',depart:'学工',contact:'14627189301',sex:'男',age:'26',salary:'12000',dateIn:'1998-2-11'},
          {num:'311',name:'Tony',depart:'学工',contact:'14627189301',sex:'男',age:'26',salary:'12000',dateIn:'1998-2-11'},
          // {num:'4',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'}
        ],
        stuInformDis:[
          {num:'110',name:'Tony',depart:'教务',contact:'14627189301',sex:'男',age:'26',salary:'12000',dateIn:'1998-2-11'},
          {num:'210',name:'Tony',depart:'教务',contact:'14627189301',sex:'男',age:'26',salary:'12000',dateIn:'1998-2-11'},
          {num:'310',name:'Tony',depart:'教务',contact:'14627189301',sex:'男',age:'26',salary:'12000',dateIn:'1998-2-11'}]
      }
    },
    methods: {
      tableCurrentChange(val){
        this.$set(this.stuInformDis,0,this.stuInform[val*3-3]);
        this.$set(this.stuInformDis,1,this.stuInform[val*3-2]);
        this.$set(this.stuInformDis,2,this.stuInform[val*3-1]);
      }
    }

  }
</script>

<style scoped>
  #staffInform{


    height: 100%;

    background-color: #ff6a00;
  }
  *{
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;

    font-size: 25px;
    overflow: hidden;
  }
  li{
    list-style-type: none;
  }
  a{
    text-decoration: none;
  }

  .el-row {
    margin-bottom: 20px;
  }
  .el-col {
    border-radius: 4px;
  }


  .search-content {
    border-radius: 4px;
    height: 220px;
    background-color: white;
    border-bottom: 1px solid #d3d3d3;
  }

  .staff-choice{
    margin-left: 20px;

    height: 220px;

  }

  .staff-box
  {
    margin-top: 80px;
    margin-left: 20px;
    width: 200px;

    font-size: 20px;
  }

  .search-button{
    position: relative;
    top: -10px;
    left: 20px;

    height: 50px;
    width: 100px;

  }

  .staff-inform{
    height: 362px;

    background-color: white;

    border-top: 1px solid #d3d3d3;
    border-radius: 4px;
  }
  .staff-inform-content{

    margin-left: 20px;
    width: 97%;

    font-size: 20px;
    line-height: 50px;
    color: gray;
  }

  .el-select >>> .el-input__inner{
    font-size: 20px;
  }


</style>



