<template>
  <div id="recruitInform">
        <el-row class="search-content">
          <el-col span="24" class="stu-choice">
              <el-input class="stu-name" placeholder="请输入学生姓名" v-model="nameVal" clearable></el-input>
            <el-date-picker class="stu-name" style="width:210px; " type="year" placeholder="请选择招生时间" v-model="yearVal"></el-date-picker>
            <span style="position: relative;left: 30px;top: -10px;font-weight: bold;">{{searchWord[0]}}</span>
            <el-select class="stu-name" style="overflow: hidden;margin-left: 20px;" v-model="stuOptions.label"  placeholder="请选择">
              <el-option align="center" style="font-size: 20px;" v-for="item in stuOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
            <el-button type="danger" class="search-button">{{searchWord[1]}}</el-button>
            <el-button type="danger" class="search-button-2">{{searchWord[2]}}</el-button>
          </el-col>
        </el-row>
        <el-row class="stu-inform">
          <el-col>
            <el-table :data="stuInformDis" class="stu-inform-content" row-style="height:70px" >
              <el-table-column prop="num" :label="informList[0]" >
              </el-table-column>
              <el-table-column prop="name" :label="informList[1]">
              </el-table-column>
              <el-table-column prop="class" :label="informList[2]" width="170" >
              </el-table-column>
              <el-table-column prop="contact" :label="informList[3]" width="210">
              </el-table-column>
              <el-table-column prop="sex" :label="informList[4]">
              </el-table-column>
              <el-table-column prop="age" :label="informList[5]" >
              </el-table-column>
              <el-table-column prop="date" :label="informList[6]" width="150">
              </el-table-column>
              <el-table-column prop="admit" :label="informList[7]">
              </el-table-column>
            </el-table>
            <el-pagination  @current-change="tableCurrentChange" style="margin-left: 500px;margin-top: 10px" page-size="3"  layout="prev,pager,next,jumper" :total="stuInform.length"  >
            </el-pagination>
          </el-col>
        </el-row>
    </div>

</template>

<script type="text/ecmascript-6">
    export default {
        name: "recruitInformPage",
        data()
        {
            return {
                searchWord:['招生人员：','搜索','重置'],
                stuOptions:[
                    {value:'1',label:'张三'},
                    {value:'2',label:'李四'},
                    {value:'3',label:'王五'},
                    {value:'4',label:'王六'}
                ],
                nameVal:[''],
                yearVal:[''],
                informList:['学号','学生','所在年级和班级','家长姓名与联系方式','性别','年龄','招生日期','招生人员'],
                stuInform:[
                    {num:'1',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'},
                    {num:'2',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'},
                    {num:'3',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'},
                    {num:'4',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'},
                    {num:'3',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'},
                    // {num:'4',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'}
                ],
                stuInformDis:[
                    {num:'1',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'},
                    {num:'2',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'},
                    {num:'3',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'}]
            }
        },
        methods: {
            tableCurrentChange(val){
                this.$set(this.stuInformDis,0,this.stuInform[val*3-3]);
                this.$set(this.stuInformDis,1,this.stuInform[val*3-2]);
                this.$set(this.stuInformDis,2,this.stuInform[val*3-1]);
            }
        }

    }
</script>

<style scoped>
  #recruitInform{


    height: 100%;

    background-color: #ff6a00;
  }
  *{
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;

    font-size: 25px;
    overflow: hidden;
  }
  li{
    list-style-type: none;
  }
  a{
    text-decoration: none;
  }

  .el-row {
    margin-bottom: 20px;
  }
  .el-col {
    border-radius: 4px;
  }


  .search-content {
    border-radius: 4px;
    height: 220px;
    background-color: white;
    border-bottom: 1px solid #d3d3d3;
  }

  .stu-choice{
    margin-left: 20px;

    height: 220px;

  }
  /*same as semester-button*/
  .stu-name
  {
    margin-top: 70px;
    margin-left: 50px;
    width: 200px;

    font-size: 20px;
  }

  .search-button{
    position: relative;
    top: 65px;
    left: 50px;

    height: 50px;
    width: 100px;

  }

  .search-button-2{
    position: relative;
    top: 65px;
    left: 50px;

    height: 50px;
    width: 100px;
  }
  .stu-inform{
    height: 362px;

    background-color: white;

    border-top: 1px solid #d3d3d3;
    border-radius: 4px;
  }
  .stu-inform-content{

    margin-left: 20px;
    width: 97%;

    font-size: 20px;
    line-height: 50px;
    color: gray;
  }

  .el-select >>> .el-input__inner{
    font-size: 20px;
  }

</style>



