<template>
  <div id="financeInform">
    <el-row class="search-content">
      <el-col span="24" class="stu-choice">
        <el-input class="stu-box" placeholder="请输入学生姓名" v-model="nameVal" clearable></el-input>
        <el-date-picker class="stu-box" style="width:230px; " type="month" placeholder="请选择收退费时间" v-model="yearVal"></el-date-picker>
        <span style="position: relative;left: 30px;top: -10px;font-weight: bold;">{{searchWord[0]}}：</span>
        <el-select class="stu-box" style="overflow: hidden;margin-left: 20px;" v-model="stuOptions.label"  placeholder="请选择">
          <el-option align="center" style="font-size: 20px;" v-for="item in stuOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <span style="position: relative;top: -10px;font-weight: bold;">{{searchWord[3]}}：</span>
        <el-select class="stu-box" style= "overflow: hidden; margin-left:0px;width:120px" v-model="chargeOptions.label"  :placeholder="chargeOptions[0].label">
          <el-option align="center" style="font-size: 20px;" v-for="item in chargeOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <span style="position: absolute;top: 140px;left:35px;font-weight: bold;">{{searchWord[4]}}：</span>
        <el-select class="semester-box" style= "" v-model="semOptions.label"  :placeholder="semOptions[0].label">
          <el-option align="center" style="font-size: 20px;" v-for="item in semOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <div class="button-box">
        <el-button type="danger" class="search-button">{{searchWord[1]}}</el-button>
        <el-button type="danger" class="search-button-2">{{searchWord[2]}}</el-button>
        </div>
      </el-col>
    </el-row>
    <el-row class="stu-inform">
      <el-col>
        <el-table :data="stuInformDis" class="stu-inform-content" row-style="height:70px" >
          <el-table-column prop="name" :label="informList[0]" width="70" >
          </el-table-column>
          <el-table-column prop="class" :label="informList[1]" width="160">
          </el-table-column>
          <el-table-column prop="contact" :label="informList[2]" width="200" >
          </el-table-column>
          <el-table-column prop="payment" :label="informList[3]">
          </el-table-column>
          <el-table-column prop="proof" :label="informList[4]" width="150">
          </el-table-column>
          <el-table-column prop="amoRec" :label="informList[5]" >
          </el-table-column>
          <el-table-column prop="relRec" :label="informList[6]" width="">
          </el-table-column>
          <el-table-column prop="date" :label="informList[7]">
          </el-table-column>
          <el-table-column prop="handler" :label="informList[8]">
          </el-table-column>
          <el-table-column  id="confirm"  prop="stat" :label="informList[9]">
          </el-table-column>
        </el-table>
        <el-pagination  @current-change="tableCurrentChange" style="margin-left: 500px;margin-top: 10px" page-size="3"  layout="prev,pager,next,jumper" :total="stuInform.length"  >
        </el-pagination>
      </el-col>
    </el-row>
  </div>

</template>

<script type="text/ecmascript-6">
  export default {
    name: "financeInformPage",
    data()
    {
      return {
        searchWord:['经办人员','搜索','重置','类目','选择学期'],
        stuOptions:[
          {value:'1',label:'张三'},
          {value:'2',label:'李四'},
          {value:'3',label:'王五'},
          {value:'4',label:'王六'}
        ],
        chargeOptions:[
          {value:'1',label:'收费'},
          {value:'2',label:'退费'},
          {value:'3',label:'作废'}
        ],
        semOptions:[
          {value:'1',label:'第一学期'},
          {value:'2',label:'第二学期'}
        ],
        nameVal:[''],
        yearVal:[''],
        informList:['学生','所在年级和班级','家长姓名与联系方式','支付方式','凭证','应收金额','实收金额','日期','经办人员','状态'],
        stuInform:[
          {name:'Tony',class:'3年A班',contact:'mom-14627189301',payment:'支付宝',proof:'xxx-xxxxxxx',amoRec:'8000',relRec:'10000',date:'2017-8-1',handler:'张三',stat:'未确认'},
          {name:'Tony',class:'3年A班',contact:'mom-14627189301',payment:'支付宝',proof:'xxx-xxxxxxx',amoRec:'8000',relRec:'10000',date:'2017-8-1',handler:'张三',stat:'未确认'},
          {name:'Tony',class:'3年A班',contact:'mom-14627189301',payment:'支付宝',proof:'xxx-xxxxxxx',amoRec:'8000',relRec:'10000',date:'2017-8-1',handler:'张三',stat:'未确认'},
          {name:'Tony',class:'3年A班',contact:'mom-14627189301',payment:'支付宝',proof:'xxx-xxxxxxx',amoRec:'8000',relRec:'10000',date:'2017-8-1',handler:'张三',stat:'未确认'},
          {name:'Tony',class:'3年A班',contact:'mom-14627189301',payment:'支付宝',proof:'xxx-xxxxxxx',amoRec:'8000',relRec:'10000',date:'2017-8-1',handler:'张三',stat:'未确认'},
          // {num:'4',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'}
        ],
        stuInformDis:[
          {name:'Tony',class:'3年A班',contact:'mom-14627189301',payment:'支付宝',proof:'xxx-xxxxxxx',amoRec:'8000',relRec:'10000',date:'2017-8-1',handler:'张三',stat:'未确认'},
          {name:'Tony',class:'3年A班',contact:'mom-14627189301',payment:'支付宝',proof:'xxx-xxxxxxx',amoRec:'8000',relRec:'10000',date:'2017-8-1',handler:'张三',stat:'未确认'},
          {name:'Tony',class:'3年A班',contact:'mom-14627189301',payment:'支付宝',proof:'xxx-xxxxxxx',amoRec:'8000',relRec:'10000',date:'2017-8-1',handler:'张三',stat:'未确认'},]
      }
    },
    methods: {
      tableCurrentChange(val){
        this.$set(this.stuInformDis,0,this.stuInform[val*3-3]);
        this.$set(this.stuInformDis,1,this.stuInform[val*3-2]);
        this.$set(this.stuInformDis,2,this.stuInform[val*3-1]);
      }
    },

  }
</script>

<style scoped>
  #financeInform{


    height: 100%;

    background-color: #ff6a00;
  }

  *{
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;

    font-size: 25px;
    overflow: hidden;
  }
  li{
    list-style-type: none;
  }
  a{
    text-decoration: none;
  }

  .el-row {
    margin-bottom: 20px;
  }
  .el-col {
    border-radius: 4px;
  }


  .search-content {
    border-radius: 4px;
    height: 220px;
    background-color: white;
    border-bottom: 1px solid #d3d3d3;
  }

  .stu-choice{
    margin-left: 20px;

    height: 220px;

  }
  /*same as semester-button*/
  .stu-box
  {
    margin-top: 70px;
    margin-left: 15px;
    width: 200px;

    font-size: 20px;
  }

/* button’s father container*/
  .button-box
  {
    position: relative;
    top:-120px;
    left:1070px;

    height: 200px;
    width: 100px;
  }

  .semester-box{
    position: absolute;
    left:155px;
    top: 140px;

    width: 150px;

  }

  .search-button{
    position:absolute;
    top:70px;


    height: 50px;
    width: 100px;

  }

  .search-button-2{
    position: absolute;
    top: 140px;
    margin-left:0px;

    height: 50px;
    width: 100px;
  }
  .stu-inform{
    height: 362px;

    background-color: white;

    border-top: 1px solid #d3d3d3;
    border-radius: 4px;
  }
  .stu-inform-content{

    margin-left: 20px;
    width: 97%;

    font-size: 20px;
    line-height: 50px;
    color: gray;
  }

  .el-select >>> .el-input__inner{
    font-size: 20px;
  }

</style>



