<template>
  <div id="checkPend">
    <el-row class="search-content">
      <el-col span="24" class="stu-choice">
        <el-input class="stu-box" style="margin-left: 200px" placeholder="请输入学生姓名" v-model="nameVal" clearable></el-input>
        <el-date-picker class="stu-box" style="width:230px; " type="month" placeholder="请选择收退费时间" v-model="yearVal"></el-date-picker>
        <span style="position: relative;left: 30px;top: -10px;font-weight: bold;">{{searchWord[0]}}：</span>
        <el-select class="stu-box" style="overflow: hidden;margin-left: 20px;" v-model="stuOptions.label"  placeholder="请选择">
          <el-option align="center" style="font-size: 20px;" v-for="item in stuOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>

        <div class="button-box">
          <el-button type="danger" class="search-button">{{searchWord[1]}}</el-button>
          <el-button type="success" class="search-button-2">{{searchWord[2]}}</el-button>
        </div>
      </el-col>
    </el-row>

    <el-row class="stu-inform">
      <el-col>
        <el-table :data="stuInformDis" class="stu-inform-content" row-style="height:70px" >
          <el-table-column prop="num" :label="informList[0]" width="70" >
          </el-table-column>
          <el-table-column prop="name" :label="informList[1]" width="100">
          </el-table-column>
          <el-table-column prop="class" :label="informList[2]" width="200" >
          </el-table-column>
          <el-table-column prop="contact" :label="informList[3]" width="200">
          </el-table-column>
          <el-table-column prop="refund" :label="informList[4]" >
          </el-table-column>
          <el-table-column prop="relRefund" :label="informList[5]" >
          </el-table-column>
          <el-table-column prop="date" :label="informList[6]" >
          </el-table-column>
          <el-table-column prop="handler" :label="informList[7]">
          </el-table-column>
          <el-table-column  prop='remark' :label="informList[8]">
          </el-table-column>
          <el-table-column :label="informList[9]">
            <el-button type="text" style="color: #f56c6c;font-size: 18px;display: block;margin-left: -5px;" @click="buttonAlert">撤销</el-button>
          </el-table-column>
        </el-table>
        <el-pagination  @current-change="tableCurrentChange" style="margin-left: 500px;margin-top: 10px" page-size="3"  layout="prev,pager,next,jumper" :total="stuInform.length"  >
        </el-pagination>
      </el-col>
    </el-row>
  </div>

</template>

<script type="text/ecmascript-6">
  export default {
    name: "checkPendPage",
    data()
    {
      return {
        searchWord:['经办人员','确定','取消','支付方式','选择学期'],
        stuOptions:[
          {value:'1',label:'张三'},
          {value:'2',label:'李四'},
          {value:'3',label:'王五'},
          {value:'4',label:'王六'}
        ],
        chargeOptions:[
          {value:'1',label:'支付宝'},
          {value:'2',label:'微信'},
          {value:'3',label:'现金'}
        ],
        semOptions:[
          {value:'1',label:'第一学期'},
          {value:'2',label:'第二学期'}
        ],
        nameVal:[''],
        yearVal:[''],
        informList:['学号','学生','所在年级和班级','联系方式','应退金额','实退金额','退费日期','经办人员','备注','操作'],
        stuInform:[
          {num:'120',name:'Tony',class:'3年A班',contact:'mom-19810980038',refund:'10000',relRefund:'8000',date:'2017-8-1',handler:'张三',remark:'现金'},
          {num:'120',name:'Tony',class:'3年A班',contact:'mom-19810980038',refund:'10000',relRefund:'8000',date:'2017-8-1',handler:'张三',remark:'现金'},
          {num:'120',name:'Tony',class:'3年A班',contact:'mom-19810980038',refund:'10000',relRefund:'8000',date:'2017-8-1',handler:'张三',remark:'现金'},
          {num:'120',name:'Tony',class:'3年A班',contact:'mom-19810980038',refund:'10000',relRefund:'8000',date:'2017-8-1',handler:'张三',remark:'现金'},
          {num:'120',name:'Tony',class:'3年A班',contact:'mom-19810980038',refund:'10000',relRefund:'8000',date:'2017-8-1',handler:'张三',remark:'现金'},
          // {num:'4',name:'Tony',class:'3年A班',contact:'mom-14627189301',sex:'男',age:'6',date:'2019-10-11',admit:'张三'}
        ],
        stuInformDis:[
          {num:'120',name:'Tony',class:'3年A班',contact:'mom-19810980038',refund:'10000',relRefund:'8000',date:'2017-8-1',handler:'张三',remark:'现金'},
          {num:'120',name:'Tony',class:'3年A班',contact:'mom-19810980038',refund:'10000',relRefund:'8000',date:'2017-8-1',handler:'张三',remark:'现金'},
          {num:'120',name:'Tony',class:'3年A班',contact:'mom-19810980038',refund:'10000',relRefund:'8000',date:'2017-8-1',handler:'张三',remark:'现金'}]
      }
    },
    methods: {
      tableCurrentChange(val){
        this.$set(this.stuInformDis,0,this.stuInform[val*3-3]);
        this.$set(this.stuInformDis,1,this.stuInform[val*3-2]);
        this.$set(this.stuInformDis,2,this.stuInform[val*3-1]);
      },
      buttonAlert(){
        this.$alert('执行操作？','提示',{
          showCancelButton:true,
          confirmButtonText:'确定',
          cancelButtonClass:'取消',
          callback:action => {
            if(action == 'confirm')
            {
              this.$message({
                type:'info',
                message:`操作成功`
              })
            }
            else{
              this.$message({
                type:'info',
                message:`已取消操作`
              })
            }
          }
        })
      }

    },
    mounted() {
      var stat = document.getElementById('confirm');
      if(this.stuInform.stat=='未确认')
        stat.style.color = 'red';
      else
        stat.style.color = 'green';
    }


  }
</script>

<style scoped>
  #checkPend{


    height: 100%;

    background-color: #ff6a00;
  }

  *{
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;

    font-size: 25px;
    overflow: hidden;
  }
  li{
    list-style-type: none;
  }
  a{
    text-decoration: none;
  }

  .el-row {
    margin-bottom: 20px;
  }
  .el-col {
    border-radius: 4px;
  }


  .search-content {
    border-radius: 4px;
    height: 220px;
    background-color: white;
    border-bottom: 1px solid #d3d3d3;
  }

  .stu-choice{
    margin-left: 20px;

    height: 230px;

  }
  /*same as semester-button*/
  .stu-box
  {
    margin-top: 70px;
    margin-left: 15px;
    width: 200px;

    font-size: 20px;
  }

  /* button’s father container*/
  .button-box
  {
    position: relative;
    top:30px;
    left:35%;

    height: 50px;
    width: 250px;
  }

  .search-button{
    position:absolute;
    top:0px;
    left: 5px;


    height: 50px;
    width: 100px;

  }

  .search-button-2{
    position: absolute;
    top: 0px;
    left:140px;

    height: 50px;
    width: 100px;
  }
  .stu-inform{
    height: 362px;

    background-color: white;

    border-top: 1px solid #d3d3d3;
    border-radius: 4px;
  }
  .stu-inform-content{

    margin-left: 20px;
    width: 97%;

    font-size: 20px;
    line-height: 50px;
    color: gray;
  }

  .el-select >>> .el-input__inner{
    font-size: 20px;
  }

</style>



