<template>
  <div id="recruitIndex">
  <div class="content-semester">
    <span>{{contentWord[0]}}</span>
  </div>
  <el-date-picker  class="semester-button" v-model="yearValue" type="year" placeholder="选择时间"></el-date-picker>
  <el-row style="position: relative;left: 30px;top: 50px">
    <el-col span="23" >
      <div class="semester-content">
        <div style="background-color:#ff6a00;">
          <span style="margin-left: 20px;line-height: 60px;font-size: 20px;letter-spacing: 1px;color: white">{{semesterInforms[0]}}</span>
        </div>
        <div class="semester-content-msg">
          <div  class="semester-content-msg-all" style="border-left: none">
            <span>{{semesterInforms[1]}}</span>
            <p >{{semesterNums[1]}}</p>
          </div>
          <div  class="semester-content-msg-all">
            <span>{{semesterInforms[2]}}</span>
            <p >{{semesterNums[2]}}</p>
          </div>
          <div  class="semester-content-msg-all">
            <span>{{semesterInforms[3]}}</span>
            <p>{{semesterNums[3]}}</p>
          </div>
          <div  class="semester-content-msg-all">
            <el-link href="#/recruitInformPage" style="height:120px">{{semesterInforms[4]}}<i class="el-icon-warning" ></i></el-link>
          </div>
        </div>
      </div>
    </el-col>
  </el-row>
  </div>
</template>

<script type="text/ecmascript-6">
    export default {
        name: "recruitIndexPage",
        data()
        {
            return {
                contentWord:['学期：'],
                semesterOptions:[
                    {value:'1',label:'2019'},
                    {value:'2',label:'2018'},
                    {value:'3',label:'2017'},
                    {value:'4',label:'2016'}
                ],
                semesterInforms:['本学期统计','新招生人数','已收学费金额','累计招生人数','条目详情'],
                semesterNums:['','30','120000','1000'],
                yearValue:['']
            }
        },
        methods: {
        }
    }
</script>

<style scoped>
  #recruitIndex{
    height: 100%;
  }

  *{

    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;

    font-size: 25px;
    overflow-x: hidden;
  }
  li{
    list-style-type: none;
  }
  a{
    text-decoration: none;
  }

  .content-semester{
    position: relative;
    top: 60px;
    left: 50px;

  }

  .semester-button{
    overflow: hidden;

    position: relative;
    top: 28px;
    left: 130px;

    font-size: 20px;

  }

  .semester-content{

    height: 300px;

    border: 1px solid #d3d3d3;
    border-radius: 5px;
  }

  .semester-content-msg{
    height: 170px;

    margin-top: 25px;

  }

  .semester-content-msg-all{

    width: 220px;
    float: left;

    margin-top: 20px;
    margin-left: 45px;
    border-left: 1px solid #d3d3d3;

    text-align: center;
    color:gray;
  }
  .semester-content-msg-all p{
    margin-top: 45px;

    font-size: 35px;
  }

</style>



