<template>
  <div id="recruit">

      <el-header  class="header" id="header">
        <div class="header-tip" id="headerTip">
          <span style="font-size: 56px;">{{headerTips[0]}}</span>
          <span style="font-size: 40px; ">{{headerTips[1]}}</span>
        </div>
        <el-popover  placement="bottom" title="标题" width="300" trigger="click" content="这是一段内容,这是一段内容,这是一段内容,这是一段内容。">
          <el-button  class="header-name" type="text" slot="reference">{{headerNames[0]}}</el-button>
        </el-popover>
        <el-link class="exit-button" type="danger" :underline="false" href=" http://localhost:8081" >{{headerNames[1]}}</el-link>
      </el-header>

    <div id="menu" style="margin-top: 30px;margin-left: 20px ">
      <el-row :gutter="50">
        <el-col :span="4"><div class="grid-content">
          <el-aside width="100%">
            <el-menu router :default-openeds="['1', '2', '3']"  background-color="#ffffff" text-color="gray" collapse-transition active-text-color="#ff6a00">
              <el-submenu index="1">
                <template slot="title"><span style="font-size: 20px; color:#ff6a00">{{menuTips[0]}}</span></template>
                <el-menu-item-group disabled="">
                  <el-menu-item index="/"  >{{menuLists[0]}}</el-menu-item>
                 <el-menu-item index="financePage"> {{menuLists[1]}}</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-submenu index="2">
                <template slot="title"><span style="font-size: 20px; color:#ff6a00">{{menuTips[1]}}</span></template>
                <el-menu-item-group>
                  <el-menu-item index="staffInformPage">{{menuLists[2]}}</el-menu-item>
                  <el-menu-item index="stuInformPage">{{menuLists[3]}}</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-submenu index="3">
                <template slot="title" ><span style="font-size: 20px; color:#ff6a00">{{menuTips[2]}}</span></template>
                <el-menu-item-group>
                  <el-menu-item index="checkPendPage">{{menuLists[4]}}</el-menu-item>
                  <el-menu-item index="unCheckPage">{{menuLists[5]}}</el-menu-item>
                </el-menu-item-group>
              </el-submenu>
          </el-menu>
          </el-aside>
        </div></el-col>
        <el-col :span="20">
          <div class="grid-content">
            <router-view></router-view>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
    export default {
        name: "recruitPage",
        data()
         {
            return {
            headerTips: ['xx幼儿园', '信息管理系统'],
            headerNames: ['用户名', '退出'],
            menuTips: ['统计', '信息查看', '审核退费'],
            menuLists: ['招生统计', '财务统计', '职工信息', '学生信息', '待审核', '未审核'],
        }
    },
    methods: {

    }
    }
</script>

<style scoped>
  #recruit{
    background-color: #ff6a00;

  }
  *{

    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;

    font-size: 25px;

    overflow: hidden;
  }
  li{
    list-style-type: none;
  }
  a{
    text-decoration: none;
  }
  .router-link-active{
    text-decoration: none;
  }

  .header{
    width: 100%;
    height: 120px!important;

    background-color:#ffffff ;

    border-bottom: 1px solid #d3d3d3;
  }

  .header-tip{
    position: absolute;
    top: 30px;
    left: 60px;

    width: 600px;
    min-width:350px ;

    font-weight: bold;
    letter-spacing: 2px;
    word-spacing: 3px;

    color:#ff6a00 ;
  }

  .header-name{
    overflow: hidden;

    position: absolute;
    top:54px;
    right:220px;

    border: none;

    font-weight: bold;

    color:gray;

  }
  .header-name:hover{
    color:gray;
  }
  .header-name:focus{
    color:gray;
  }

  .exit-button{
    position: absolute;
    top: 47px;
    right: 80px;

    font-weight: bold;
  }
  menu-tips{
    position: relative;
    left: -60px;
    font-size: 40px;
  }

  .el-submenu__title{
    font-size: 25px;
  }
  .el-menu-item {
    font-size: 15px;
  }
  .el-row {
    margin-bottom: 20px;
  }
  .el-col {
    border-radius: 4px;
  }

  .grid-content {
    border-radius: 4px;
    height: 600px;
    background-color: white;
  }





</style>



