<template>
  <div id="recruitIndex">
    <div class="content-semester">
      <span>{{contentWord[0]}}</span>
    </div>
    <el-date-picker  class="semester-button" v-model="yearValue" type="year" placeholder="选择时间"></el-date-picker>
    <el-row style="position: relative;left: 30px;top: 50px">
      <el-col span="23">
        <div class="semester-content">
          <div style="background-color:#ff6a00;color: white ">
            <span style="margin-left: 20px;line-height: 60px;font-size: 20px;letter-spacing: 1px;">{{semesterInforms[0]}}</span>
          </div>
          <div class="semester-content-msg">
            <div  class="semester-content-msg-all" style="border-left: none">
              <span>{{semesterInforms[1]}}</span>
              <p >{{semesterNums[1]}}</p>
            </div>
            <div  class="semester-content-msg-all">
              <span>{{semesterInforms[2]}}</span>
              <p >{{semesterNums[2]}}</p>
            </div>
            <div  class="semester-content-msg-all">
              <span>{{semesterInforms[3]}}</span>
              <p>{{semesterNums[3]}}</p>
            </div>
            <div  class="semester-content-msg-all">
              <el-link href="#/financeInformPage"style="height:92px">{{semesterInforms[4]}}<i class="el-icon-warning" ></i></el-link>
            </div>
          </div>
        </div>
      </el-col>
      <el-col span="23">
        <div class="semester-content" >
          <div class="chart-msg">
            <h1>各项指标</h1>
            <p>{{chartMsg[0]}}:{{semesterNums[0]}}</p>
            <p>{{chartMsg[1]}}:{{semesterNums[1]}}</p>
            <p>{{chartMsg[2]}}:{{semesterNums[2]}}</p>
            <p>{{chartMsg[3]}}:{{semesterNums[3]}}</p>
          </div>
          <div  class="charts"  id="charts" :style="{width:'300px',height:'200px',overflow:'hidden',padding:'0px 30px'}"></div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script type="text/ecmascript-6" >
    export default {
        name: "financeIndexPage",
        data()
        {
            return {
                contentWord:['学期：'],
                semesterOptions:[
                    {value:'1',label:'2019'},
                    {value:'2',label:'2018'},
                    {value:'3',label:'2017'},
                    {value:'4',label:'2016'}
                ],
                semesterInforms:['本学期财务情况','应收费用','实际收费','退费金额','条目详情'],
                semesterNums:['10','10000','8000','1000'],
                yearValue:[''],
                chartMsg:['财务记录总数','收费','退费','作废'],
            }
        },
        methods: {

        }
        ,
      mounted() {
          var myChart = this.echarts.init(document.getElementById('charts'));

         myChart.setOption( {
          // title: {
          //   left: 'center'
          // },
           color:['#37a2da','#67e0e3','#ffdb5c'],
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b} : {c} ({d}%)'
          },
          legend: {
            orient: 'vertical',
            left: 'left',
            data: ['收费', '退费', '作废', ]
          },
          series: [
            {
              name: '收支情况',
              type: 'pie',
              radius: '75%',
              center: ['50%', '50%'],
              data: [
                {value: this.semesterNums[1], name: '收费'},
                {value: this.semesterNums[2], name: '退费'},
                {value: this.semesterNums[3], name: '作废'},
              ],
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        });

      }

    }
</script>

<style scoped>
  #recruitIndex{
    height: 100%;
  }

  *{
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;

    font-size: 25px;
    overflow-x: hidden;
  }
  li{
    list-style-type: none;
  }
  a{
    text-decoration: none;
  }

  .content-semester{
    position: relative;
    top: 60px;
    left: 50px;

  }

  .semester-button{
    overflow: hidden;

    position: relative;
    top: 28px;
    left: 130px;

    font-size: 20px;
  }

  .semester-content{

    height: 220px;

    border: 1px solid #d3d3d3;
    border-radius: 5px;
  }

  .semester-content-msg{
    height: 130px;

    margin-top: 20px;
  }

  .semester-content-msg-all{

    width: 220px;
    float: left;

    margin-top: 10px;
    margin-left: 45px;
    border-left: 1px solid #d3d3d3;

    text-align: center;
    color:gray;
  }
  .semester-content-msg-all p{
    margin-top: 20px;

    font-size: 35px;
  }

  .chart-msg{
    position: relative;
    float: left;
    margin-top: 30px;
    margin-left: 120px;
  }

  .chart-msg p{
    font-size: 20px;

    color: gray;
  }

  .charts{
   float: right;
    margin-top: 20px;
   margin-right: 200px;
  }
</style>



